import * as ethUtil from 'ethereumjs-util'
import * as sigUtil from 'eth-sig-util'
import {useWallet} from "../use_wallet";
import {useWeb3} from "../provider/web3_provider";
import {useCallback} from "react";
import {Web3Provider} from '@ethersproject/providers'


export const useSignFn = (): (msgData: any) => Promise<string> => {
    const {account} = useWallet();
    const {walletConnect,library} = useWeb3()


    const handleFn = useCallback(async (msgData: any) => {
        if (!account) return;
        let params = [account, msgData]
        if (walletConnect) {
            let data = await walletConnect.signTypedData(params);
            console.log('TYPED SIGNED:' + JSON.stringify(data))
            const recovered = sigUtil.recoverTypedSignature_v4({data: JSON.parse(msgData), sig: data})
            console.log(ethUtil.toChecksumAddress(recovered), account)
            return data;
        }
        let data = await sendAsync(library, params, account);
        return data;
    }, [library])

    return handleFn
}





async function sendAsync(web3: Web3Provider, params: any[], account: string): Promise<any> {
    let method = 'eth_sign'

    let result;
    try {
        console.log(web3)

        result = await web3.send(method, params);
        console.log(result)

        console.log('TYPED SIGNED:' + JSON.stringify(result))
        const recovered = sigUtil.recoverTypedSignature_v4({data: JSON.parse(params[1]), sig: result})
        console.log(ethUtil.toChecksumAddress(recovered), account)
    } catch (e) {
        throw  e;
    }

    return result;

}


export const useLedgerSignFn = (): (msgData: any) => Promise<string> => {
    const {account, ethereum} = useWallet();


    const handleFn = useCallback(async (msgData: any) => {
        if (!account) return;

        let method = 'eth_signTypedData_v4'
        const params = [account, msgData];
        let _hax;
        try {
            _hax = await ethereum.send(method, [account, msgData]);
            console.log(_hax);
            alert('sign success:' + _hax.result);
            const recovered = sigUtil.recoverTypedSignature_v4({data: JSON.parse(params[1]), sig: _hax.result})
            console.log(ethUtil.toChecksumAddress(recovered) == account);
            alert(`sign verification:${ethUtil.toChecksumAddress(recovered) == account} 
            address: ${ethUtil.toChecksumAddress(recovered)}
            `);

        } catch (e) {
            if (e.code == 4001) {
                throw  e;
            }
            console.log(e);
            alert('sign fail')
        }

        return _hax.result;
    }, [ethereum])

    return handleFn
}






